.memory {
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.box{
  text-align: center;
}

input {
  background: #101010;
  border: 2px solid #a2a2a2;
  color: #f2f2f2
}

.img1 {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

}

.img2{
  height: 100vh;
  width: calc(100% - 15px);
  position: absolute;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

hr {
  background: #fff;
}

ul {
    list-style: none;
    margin-left: 0;
    padding-left: 1.2em;
    text-indent: -1.2em;
    color: lime;
}

li:before {
    content: ">";
    display: block;
    float: left;
    width: 1.2em;
    color: lime;
}



